.App {
  text-align: center;
}
* {
  box-sizing: border-box;
}


html {
  background: rgb(228, 123, 139);
}

p {
  font-weight: 700;
  color: white;
  text-shadow: 1px 1px black;
  padding: 1rem;
}

button {
  border-radius: 0.7rem;
  padding: 0.7rem;
  background: linear-gradient(lightgreen, yellow);
  border: black 2px solid;
  font-weight: 700;
  margin: 2rem;
}

img {
  height: 10rem;
  border-radius: 10px;
  margin: 1rem;
}
